header {
  background-attachment: scroll;
}

.video-container video { 
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  transform: translate(-50%, -50%);
}

.overlay {
	  height: 100%;
    width: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: -1;
    background: black;
    opacity: 0.3;
}

.title h1 {
	text-align: center;
	color: white;
	font-family: 'Pacifico', cursive;
	font-size: 6em;
	z-index: 1;
}